<template>
  <div class="visitorInvite">
    <!-- 头部 -->
    <navigation-bar  @onLeftClick="onClickLeft" :pageName="title" :bothWidth="headerItemWidth" ></navigation-bar>
    <div class="visitorInviteContent">
      <div class="visitorList">
        <div class="inviteBtn" @click="showDoors()">我要邀请</div>
      </div>
      <div class="visitorContent">
        <div class="bg">
          <div class="bgList">
            <h5 class="inviteHistory">邀请记录</h5>
          </div>
        </div>
        
        <van-list
          v-model="loading"
          :finished="finished"
          :immediate-check="false"
          @load="onLoad"
          :offset="600"
        >
        <div class="bgListContent" v-for="(visitor,index) in visitorData" :key="index">
          <div class="intoHistory">
            <div class="kindList">
              <div class="intoLeft">{{visitor.ownerNickName}}</div>
              <div class="timeRight">步行进入</div>
            </div>
            <div class="visitors">
              <div class="visitorName">{{visitor.sendTime}}</div>
            </div>
          </div>
          <div class="baseLines"></div>
        </div>
        </van-list>

      </div>
    </div>

    <div id="shareCover" ref="shareCover" v-show="isShowShare">
      <div class="content">
        <h4>访客邀请二维码</h4>
        <div class="timeSpan">有效期至：{{endTime}}</div>
        <div style="text-align:center">
          <canvas id="phoneDoorQrcode" style="height:44px;width:44px;margin:0 auto"></canvas>
        </div>
        <div class="img">
          <div @click="jumpToWeChat()">
            <div class="shareBtn">分享至微信</div>
          </div>
        </div>
      </div>
      <div style="width:100%;text-align:center;">
        <img
          @click="closeCover()"
          :src="visitor_btn_closed"
          alt
          width="35"
          style="margin-top:20px;"
        />
      </div>
    </div>

    <!-- 小区选择弹框 -->
    <van-popup
      v-model="showDoorSelectView"
      @click-overlay="selectDoorCancel"
      position="bottom"
      :style="{ height: '36%' }"
    >
      <div class="areaselect-topbar">
        <div @click="selectDoorCancel">取消</div>
        <span @click="selectDoorOk">确定</span>
      </div>
      <van-picker :columns="doorName" :default-index="selectDoorIndex" @change="onChange" />
    </van-popup>
  </div>
</template>

<script>
import {
  getVisitorListService,
  getLinglingVisitorQrcodeService,
  addInviteService
} from "../../../services/visitorInviteService";
import {
  getDoorListService,
  addRecordService
} from "../../../services/phoneDoorService";
import QRCode from "qrcode"; //引入生成二维码插件
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "visitorInvite",
  components:{
      NavigationBar
  },
  data() {
    return {
      isHas: 0,
      visitorData: [],
      visitorDefault: false,
      endTime:"",
      total: 0, //访问总数
      visitorTotal: [], //门禁列表
      lingLingId: "", // 令令设备ID
      isShowShare: false,
      showDoorSelectView: false, //门禁选择框
      doorName: [], //门禁名称
      selectDoorIndex: 0,
      loading:false,
      finished: false,
      refreshing:false,
      reqData:{
        ownerCode: this.$userInfo.OWNER_CODE, //业主代码
        areaCode: this.$userInfo.AREA_CODE, //小区代码
        pages: 1, //当前页
        rows: 10 //每页显示条数
      },
      headerItemWidth:{
          width:'84px'
      },
      title:'访客邀请',
      visitor_btn_closed: require("../../../assets/images/home/property/visitorInvite/visitor_btn_closed.png")
    };
  },
  mounted() {
    this.qrCanvas = document.getElementById("phoneDoorQrcode");
    this.getVisitorList();
    this.getDoorList();
  },
  methods: {
    showDoors() {
      if (this.doorName.length <= 0) {
        this.$toast("暂无可用门禁");
      } else {
        this.showDoorSelectView = true;
      }
    },
    getVisitorList() {
      // this.reqData.page = this.reqData.pages+'';
      getVisitorListService(this.reqData)
        .then(result => {
          //请求成功
          this.visitorDefault = false; //设置默认default页面
          this.loading = false;
          if(result.data.list.length>0){
             result.data.list.forEach((item, index) => {
               this.visitorData.push(item);
            });
          }
          if (this.reqData.pages < result.data.total/10) {
            this.finished = false;
          } else {
            this.finished = true;
          }
          this.reqData.pages++;
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.loading = false;
          this.finished = true;
          this.$toast("网络异常，请稍后重试！");
        });
    },
    onLoad(){
      this.getVisitorList();
    },
    getDoorList() {
      let reqData = {
        areaCode: this.$userInfo.AREA_CODE,
        buildCode: this.$userInfo.BUILD_CODE,
        roomCode: this.$userInfo.ROOM_CODE,
        ownerCode: this.$userInfo.OWNER_CODE,
        unitNo: this.$userInfo.UNIT_NO
      };
      getDoorListService(reqData)
        .then(result => {
          //请求成功
          if (result.resultCode == 0) {
            this.visitorTotal = result.data;

            for (var i = 0; i < this.visitorTotal.length; i++) {
              if (this.visitorTotal[i].deviceMfr == 2) { //deviceMfr = 2 令令
                if ((this.visitorTotal[i].openType & 2) == 2) {
                  //2:二维码开门
                  this.doorName.push(this.visitorTotal[i].deviceName);
                  this.lingLingId = this.visitorTotal[i].linglingId;
                }
              }
            }
          } else {
            this.$toast(result.msg);
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },

    //点击门禁选择器
    selectDoor: function() {
      this.showDoorSelectView = true;
    },
    //选择门禁取消按钮点击
    selectDoorCancel: function() {
      this.showDoorSelectView = false;
    },
    //选择门禁确认按钮点击
    selectDoorOk: function() {
      this.showDoorSelectView = false;
      if (this.selectDoorIndex != undefined) {
        this.getQrcode(this.visitorTotal[this.selectDoorIndex]);
      }
    },
    //门禁选择器当前位置
    onChange: function(picker, value, index) {
      this.selectDoorIndex = index;
    },
    closeCover() {
      this.isShowShare = false;
    },
    onClickLeft() {
      this.$router.back();
    },
    getEndTime(timestamp){
      this.endTime = new Date((timestamp+86400000));//add 24hours-> 86400000ms
      var year = this.endTime.getFullYear()+'年';
      var month = this.endTime.getMonth()+1+'月';
      var date = this.endTime.getDate()+'日';
      var hour = this.p(this.endTime.getHours());
      var minute = this.p(this.endTime.getMinutes());
      var second = this.p(this.endTime.getSeconds());
      this.endTime  = [year,month,date].join('')+[hour,minute,second].join(':');
    },
    p(s) {
      return s < 10 ? '0' + s : s
    },
    /* 获取二维码 */
    getQrcode(item) {
      //记录device信息
      this.deviceId = item.deviceId;
      this.deviceName = item.deviceName;
      //初始化
      this.qrcodeUrl = "";
      var timestamp = new Date().getTime();
      this.getEndTime(timestamp);
      console.log("测试"+this.endTime);
      var sdkKeyArray = new Array();
      sdkKeyArray.push(item.sdkKey);
      var reqData = {
        lingLingId: this.lingLingId,
        sdkKeys: sdkKeyArray,
        startTime: timestamp,
        endTime: 1440, //24小时
        effectNumber: 0, //不限次数
        strKey: "11111111"
      };
      getLinglingVisitorQrcodeService(reqData)
        .then(result => {
          //请求成功
          if (result.resultCode==0) {
            if (result.data.qrcodeKey != "") {
              this.isShowShare = true;
              QRCode.toCanvas(this.qrCanvas, result.data.qrcodeKey);
            } else {
              this.$toast("获取二维码失败，请稍后重试！");
            }
          } else {
            this.$toast("获取二维码失败，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    /* 跳转至微信 */
    jumpToWeChat() {
      // 新增开门记录方法
      this.addInvite();
      this.addRecord(this.deviceId, this.deviceName, 3, 2, 1, 1, "访客邀请-微信");

      // 分享图片
      var qrcodeUrl = this.qrCanvas.toDataURL("image/png");
      if (qrcodeUrl != "") {
        Wechat.share(
          {
            message: {
              title: "分享钥匙",
              description: "微信钥匙分享",
              //thumb: "",
              media: {
                type: Wechat.Type.IMAGE,
                image: qrcodeUrl
              }
            },
            scene: 0 // share to Timeline
          },
          () => {
            //刷新列表
            this.getVisitorList();
          },
          reason => {
            //失败
            this.$toast(reason);
          }
        );
        this.isShowShare = false;
      }
    },
    addInvite() {
      // 获取需要分享的数据
      var phoneModel = device.model;
      var phoneType = device.platform;
      var visitorData = {
        OWNER_CODE: this.$userInfo.OWNER_CODE, //业主代码
        AREA_CODE: this.$userInfo.AREA_CODE, //小区代码
        AREA_NAME: this.$userInfo.AREA_NAME, //小区名称
        OWNER_CODE: this.$userInfo.OWNER_CODE, //业主代码
        OWNER_NAME: this.$userInfo.OWNER_NAME, //业主名称
        OWNER_NICK_NAME: this.$userInfo.OWNER_NICK_NAME, //业主昵称
        PHONE: this.$userInfo.PHONE, //手机号
        ROOM_CODE: this.$userInfo.ROOM_CODE, //房间代码
        ROOM_NAME: this.$userInfo.ROOM_NAME, //房间名称
        OPEN_TYPE: "3", // 开门方式：1蓝牙，2远程开门，3微信分享，4短信分享
        DEVICE_ID: this.deviceId, // 设备编号
        DEVICE_NAME: this.deviceName, // 设备名称
        PHONE_MODEL: phoneModel, // 开门手型号
        PHONE_TYPE: phoneType // 开门手机类型
      };
      addInviteService({ data: JSON.stringify(visitorData) }).then(res => {
        if (res.success) {
          console.log("访客邀请记录成功!");
        }
      });
    },
    // 新增开门记录方法
    addRecord(
      deviceId,
      deviceName,
      openType,
      deviceMan,
      doorType,
      openSucceed,
      remarks
    ) {
      //返回开门结果
      var phoneModel = device.model;
      var phoneType = device.platform;
      var data = {
        areaCode: this.$userInfo.AREA_CODE, //小区代码
        areaName: this.$userInfo.AREA_NAME, //小区名称
        buildCode: this.$userInfo.BUILD_CODE, //楼座代码
        buildName: this.$userInfo.BUILD_NAME, //楼座名称
        unitNo: this.$userInfo.UNIT_NO, //单元号
        roomCode: this.$userInfo.ROOM_CODE, //房间代码
        ownerCode: this.$userInfo.OWNER_CODE, //业主代码
        ownerName: this.$userInfo.OWNER_NAME, //业主名称
        ownerNickName: this.$userInfo.OWNER_NICK_NAME, //业主昵称
        phone: this.$userInfo.PHONE, //手机号
        phoneModel: phoneModel, // 开门手型号
        phoneType: phoneType, // 开门手机类型
        deviceId: deviceId, // 设备编号
        deviceName: deviceName, // 设备名称
        openType: openType, // 开门方式：1蓝牙，2远程开门，3二维码，0其它
        deviceMan: deviceMan, //设备厂家 0其它 1立林 2令令 3业主宝 4海康
        doorType: doorType, //门口类型 0大门 1单元门
        validTime: 1440, //开门话费时间
        openSucceed: openSucceed, //开门是否成功 0失败 1成功
        remarks: remarks //结果备注
      };
      addRecordService(data).then(res => {
        if (res.resultCode == "0") {
          console.log("访客邀请记录成功!");
        }
      });
    }
  }
};
</script>

